import React from "react";
import { useParams } from "react-router-dom";
import MainContent from "../../components/MainContent/MainContent";
import ArticlePipeline from "./ArticlePipeline/ArticlePipeline";
import MyArticles from "./MyArticles/MyArticles";
import ArticleChat from "./ArticleChat/ArticleChat";
import ArticleBrief from "./ArticleBrief/ArticleBrief";

const ArticlePage = () => {
  const { articleId } = useParams();
  const articleTabs = [
    <ArticlePipeline
      tabId="ArticlePipeline"
      tabLabel=" "
      key="ArticlePipeline"
      articleId={articleId}
    />,
    <ArticleChat tabId="article-chat" key="article-chat" tabLabel="">
      
    </ArticleChat>,
    <ArticleBrief
      articleId={articleId}
      tabId="article-brief"
      key="article-brief"
      tabLabel=""
    >
      
    </ArticleBrief>,
    <ArticleChat tabId="tab-4" tabLabel="" key="tab-4">
      
    </ArticleChat>,
  ];

  return (
    <MainContent title={getTitle(articleId)}>
      {!articleId ? <MyArticles /> : articleTabs}
    </MainContent>
  );
};

const getTitle = (articleId) => {
  if (!articleId) return " ";
  return " " + articleId;
};
export default ArticlePage;
